export const removeUndefinedAndDups = (arr) => {
  return [...new Set(arr)].filter(item => item)
};

export const getDefaultChartOptions = () => {
  return Object.assign({}, {
    chart:{
      type: 'areaspline',
      zoomType: 'x',
    },
    title: {
      text: null
    },
    xAxis: {
      type: 'datetime',
    },
    tooltip: {
      xDateFormat: 'Counts'
    },
    plotOptions: {
      areaspline: {
        pointStart: 13,
        marker: {
          enabled: false,
          symbol: 'circle',
          radius: 2,
          states: {
            hover: {
              enabled: true
            }
          }
        },
      }
    },
    series: []
  })
};

export const getInitialData = () => {
  return JSON.parse(sessionStorage.getItem("ll-initial-data")) || {};
}



