import * as UnprotectedWebUtil from './UnprotectedWebUtil'
import * as dom from './dom'

export const setupClickTrackingOnIframe = (campaignId, prospectId, impressionId) => {
  console.log("creative has script tag, setting up click on IFRAME", campaignId, prospectId, impressionId);
  dom.addClickTrackingOnIframe(campaignId, prospectId, impressionId);
}

export const setupClickTrackingOnContainer = (campaignId, prospectId, impressionId) => {
  console.log("creative has NO script tag but has url, setting up click on CONTAINER", campaignId, prospectId, impressionId);
  dom.addClickTrackingOnContainer(campaignId, prospectId, impressionId);
}



