import $ from 'jquery'
import * as UnprotectedWebUtil from './UnprotectedWebUtil'
import postscribe from 'postscribe'

export const buildCreativeWithImage = (imgUrl) => {
  const creativeElem = $("<div class='creative'>");
  creativeElem.css("background-image", `url("${imgUrl}")`);
  $("#container").append(creativeElem);
};

export const buildCreativeWithScript = (scriptTag) => {
  const creativeElem = $("<div class='creative'>");
  $("#container").append(creativeElem);
  postscribe(creativeElem, scriptTag);
};

export const setupWidthHeightOfContainer = (width,height) => {
  $("#container").css("width", `${width}px`);
  $("#container").css("height", `${height}px`);
};

export const buildPreRoll = (width, height) => {
  const preRollElem = $(
    "<div class='pre_roll'>" +
      "<div class='user_logo'></div>" +
      "<div class='account_logo'></div>" +
    "</div>"
  );
  preRollElem.addClass(`w${width}xh${height}`);
  $("#container").append(preRollElem);
};

export const updateAccountLogo = (url) => {
  $("#container .pre_roll .account_logo").css("background-image", `url("${url}")`);
};

export const updateUserLogo = (url) => {
  $("#container .pre_roll .user_logo").css("background-image", `url("${url}")`);
};

export const hideCreative = () => {
  $(".creative").hide();
};

export const showPreRoll = () => {
  $(".pre_roll").show();
};

export const smoothlyShowCreative = () => {
  console.log("in smooth showing after its time");
  $('.pre_roll').fadeOut(2000, function(){
    $('.creative').fadeIn(2000);
  });
};

export const setupShowingCreativeOnMouseOver = () => {
  $(".pre_roll").mouseover(evt => {
    console.log("got mouse movement. will smoothly show creative");
    smoothlyShowCreative();
  });
  $(".pre_roll").on("touchstart", (e) => {
    console.log("got touch. will smoothly show creative");
    smoothlyShowCreative();
  })
};

export const updateContainerUrl = (url) => {
  $("a").attr("target","_blank").attr("href",url);
};

export const addClickTrackingOnContainer = (campaignId, prospectId, impressionId) => {
  $("#container").click(function(){
    console.log("creating click: ", campaignId, prospectId, impressionId);
    UnprotectedWebUtil.createClick(campaignId, prospectId, impressionId);
  });
};

export const addClickTrackingOnIframe = (campaignId, prospectId, impressionId) => {
  console.log("** in addClickTrackingOnIframe with: ", campaignId, prospectId, impressionId);
  window.setTimeout(() => {
    if($("#container iframe").length > 0){
      console.log("** iframe is ready now. adding handlers");
      setupHandlersForIframeClickTracking(campaignId, prospectId, impressionId);
    }else{
      console.log("** iframe is NOT ready. re-running self with timeout");
      window.setTimeout(addClickTrackingOnIframe.bind(this, campaignId, prospectId, impressionId), 2500);
    }
  }, 2500);
};

const setupHandlersForIframeClickTracking = (campaignId, prospectId, impressionId) => {
  var iframeMouseOver = false;
  $("#container iframe").mouseover(evt => {
    console.log("** there is mouse over on iframe ad");
    iframeMouseOver = true;
    $(window).focus();
  });
  $("#container iframe").mouseout(evt => {
    console.log("** there is mouse out on iframe ad");
    iframeMouseOver = false;
    $(window).blur();
  });
  $(window).blur(evt => {
    if(iframeMouseOver == true){
      console.log("** we have detected a click on iframe ad. creating the click: ", campaignId, prospectId, impressionId);
      UnprotectedWebUtil.createClick(campaignId, prospectId, impressionId);
    }
  });
};




