import $ from 'jquery'

import * as UnprotectedWebUtil from './ad_unit/UnprotectedWebUtil'
import * as helpers from 'js/config/helpers'
import * as dom from './ad_unit/dom'
import * as creative from './ad_unit/creative'
import * as campaign from './ad_unit/campaign'
import * as geolocate from './ad_unit/geolocate'
import * as prospect from './ad_unit/prospect'
import * as impression from './ad_unit/impression'
import * as click from './ad_unit/click'
import QueryString from 'query-string'
import Bugsnag from './lib/bugsnag-3'
import {makeErrorPrettyObject} from './config/helpers'

__webpack_public_path__ = `${CLOUDFRONT_URL}/`
require("../css/ad_unit.css");

Bugsnag.apiKey = BUGSNAG_JAVASCRIPT_KEY;

const width = window.llAdWidth;
const height = window.llAdHeight;
const campaignId = window.llCampaignId;
const cookieName = `listenloop_abm_campaign_${campaignId}`;
var prospectId, creativeId, impressionId;
var creativeObj, campaignObj, prospectObj;

dom.setupWidthHeightOfContainer(width,height);

campaign.getCampaign(campaignId).then(
  response => {
    console.log(`got response for campaign: ${response.data}`);
    campaignObj = response.data;
    dom.updateContainerUrl(campaignObj.destination_url);
    return campaignObj;
  }
).then(
  response => {
    if(window.llCreativeId){
      return creative.getCreative(window.llCreativeId);
    }else{
      return creative.getActiveCreative(response,width, height);
    }
  }
).then(
  response => {
    console.log("got active creative: " , response.data)
    creativeObj = response.data;
    creativeId = creativeObj.id;
    creative.buildCreative(creativeObj, width, height);
    creative.setupPreRoll(campaignObj, creativeObj, width, height);
    return creativeObj;
  }
).then(
  creative => {
    console.log(`creative ${creative.id} has been shown`);
    return prospect.setupProspect(cookieName, campaignId, creativeId);
  }
).then(
  response => {
    console.log("got prospect id: ", response.data.id);
    prospectObj = response.data;
    prospectId = response.data.id;
    const params = QueryString.extract(window.location.href);
    const paramsObj = QueryString.parse(params);
    const xurl = paramsObj["xurl"];
    const referrer = window.document.referrer;
    if(xurl && (
                (xurl.indexOf("sitescout.com/test") >= 0) ||
                (xurl.indexOf("{PAGE_URL}") >= 0)
               )
      ){
      console.log(`this is a TEST impression, lets not record it. xurl - ${xurl}`);
      return Promise.resolve({data: {id: null}})
    }else if(referrer && (
                            (referrer.indexOf("localhost") >= 0) ||
                            (referrer.indexOf("abm.listenloop.com") >= 0) ||
                            (referrer.indexOf("clarityad") >= 0) ||
                            (referrer.indexOf("preview.pixel.ad") >= 0) ||
                            (referrer.indexOf("listenloop.sitescoutadserver.com/preview") >= 0) ||
                            (referrer.indexOf("listenloop.sitescoutadserver.com/admin/placement") >= 0)
                         )
            ){
      console.log(`this is a TEST impression. lets not record it. referrer - ${referrer}`)
      return Promise.resolve({data: {id: null}})
    }else {
      console.log(`this is a REAL impression, lets record it. xurl - ${xurl}`);
      return impression.createImpression(campaignId, prospectId, creativeId);
    }
  }
).then(
  response => {
    console.log("got impression id: ", response.data.id);
    impressionId = response.data.id;
    if(!impressionId){
      console.log("got no impressionId, skipping setting up of click tracking");
      return Promise.resolve();
    }else if(creativeObj.script_tag){
      return click.setupClickTrackingOnIframe(campaignId, prospectId, impressionId)
    }else if(creativeObj.url){
      return click.setupClickTrackingOnContainer(campaignId, prospectId, impressionId)
    }
  }
).then(
  response => {
    console.log("click handler has been setup:");
    return prospect.getReassignStatus(campaignId, prospectId)
  }
).then(
  response => {
    console.log("got prospects reassign status: ", response);
    if(response == true){
      return prospect.reassignProspect(campaignId, prospectId)
    }else {
      return Promise.resolve({data: prospectObj})
    }
  }
).then(
  response => {
    console.log("** prospect and its account setup has completed: ", response.data);
    prospectObj = response.data;
    if(prospectObj && prospectObj.account_logo_url){
      dom.updateAccountLogo(prospectObj.account_logo_url);
    }
  }
).catch(
  error => {
    console.error("something went wrong while setting up ad unit: ", error);
    console.error(error);
    const prettyError = makeErrorPrettyObject("AdUnitErrror", error);
    console.log(prettyError);
    Bugsnag.notify("AdUnitError", "Error while displaying Ad", prettyError, "error");
  }
)


