import $ from 'jquery'
import * as UnprotectedWebUtil from './UnprotectedWebUtil'
import * as dom from './dom'
import Bugsnag from '../lib/bugsnag-3'
import {makeErrorPrettyObject} from '../config/helpers'

export const getActiveCreative = (campaignObj, width, height) => {
  const campaignId = campaignObj.id;
  return UnprotectedWebUtil.getActiveCreative(campaignId, width, height).then(
    response => {
      console.log("UnprotectedWebUtil.getActiveCreative gave result: ", response.data);
      return response;
    },
    error => {
      console.error("error while getting creative to show: ", error, campaignId, width, height);
      const prettyError = makeErrorPrettyObject("ShowCreativeError", error);
      console.log(prettyError);
      Bugsnag.notify("ShowCreativeError", "Could not find a creative to show",
        {campaignId, width, height}, "error");
      return Promise.reject(error);
    }
  )
};

export const getCreative = (creativeId) => UnprotectedWebUtil.getCreative(creativeId);

export const buildCreative = (creativeObj) => {
  if(creativeObj == null){
    Bugsnag.notify("ShowCreativeError", "Could not find a creative to show", {width, height}, "error");
  }else if(creativeObj.script_tag){
    console.log("got the creative script tag: ", creativeObj.script_tag, " and id: ", creativeObj.id);
    dom.buildCreativeWithScript(creativeObj.script_tag)
  }else if(creativeObj.url){
    console.log("got the creative url: ", creativeObj.url, " and id: ", creativeObj.id);
    dom.buildCreativeWithImage(creativeObj.url);
  }

  if(creativeObj.destination_url){
    console.log(`this creative also has its own destination url. will use that. ${creativeObj.destination_url}`);
    dom.updateContainerUrl(creativeObj.destination_url);
  }
};

export const setupPreRoll = (campaignObj, creativeObj, width, height) => {
  if(creativeObj.envelope == true){
    dom.buildPreRoll(width, height);
    if(campaignObj && campaignObj.user_logo_url){
      dom.updateUserLogo(campaignObj.user_logo_url);
    }
    dom.hideCreative();
    dom.showPreRoll();

    window.setTimeout(() => {
      dom.smoothlyShowCreative();
    }, 15000);

    dom.setupShowingCreativeOnMouseOver();
  }
}




