import * as UnprotectedWebUtil from './UnprotectedWebUtil'

export const getLatLong = () => {
  return UnprotectedWebUtil.geolocate().then(
    response => {
      if(response.data && response.data.location){
        const lat = response.data.location.lat;
        const long = response.data.location.lng;
        return {
          lat, long
        }
      }else{
        return null;
      }
    },
    error => {
      console.log("error while getting lat long: ", error);
      return Promise.reject(error);
    }
  );
};


