import * as UnprotectedWebUtil from './UnprotectedWebUtil'
import Cookies from 'js-cookie'
import * as geolocate from './geolocate'

export const getReassignStatus = (campaignId, prospectId) => {
  console.log("getting reassign status for: ", campaignId, prospectId)
  return UnprotectedWebUtil.unprotectedReassignStatus(campaignId, prospectId).then(
    response => {
      if(response.data){
        return response.data.status
      }else{
        return false;
      }
    }
  )
};

export const setupProspect = (cookieName, campaignId) => {
  console.log("lets get a prospect for: ", cookieName, campaignId);
  return UnprotectedWebUtil.createProspect(campaignId);
};

export const reassignProspect = (campaignId, prospectId) => {
  console.log("doing reassign of prospect, starting by calling google ", campaignId, prospectId)
  return geolocate.getLatLong().then(
    latLongObj => {
      console.log("in success after getLatLong from google");
      const lat = latLongObj.lat;
      const long = latLongObj.long;
      console.log("got lat long to reassign prospect: ", lat, long);
      return UnprotectedWebUtil.reassignProspectToAccount(campaignId, prospectId, lat, long)
    },
    error => {
      console.error("got an error from google looking up: ",campaignId, prospectId, error);
      Bugsnag.notify("GoogleGeolocationError", "Error getting lat long from google", error, "error");
      return UnprotectedWebUtil.reassignProspectToAccount(campaignId, prospectId, null, null);
    }
  )
}


